<template>
  <div class="home">
    <!-- 英雄区域 -->
    <section class="hero">
      <div class="page-container">
        <div class="hero-content">
          <h1 class="hero-title">
            <span class="title-line">艺术与生活的</span>
            <span class="title-line highlight">完美交融</span>
          </h1>
          <p class="hero-description">
            欢迎来到孙可馨的个人空间，这里记录着我的创作历程与生活点滴
          </p>
          <div class="hero-actions">
            <t-button theme="primary" size="large" @click="$router.push('/portfolio')">
              浏览作品
            </t-button>
            <t-button theme="default" size="large" @click="$router.push('/about')">
              了解更多
            </t-button>
          </div>
        </div>
      </div>
    </section>

    <!-- 特色作品展示 -->
    <section class="featured-works">
      <div class="page-container">
        <h2 class="section-title">精选作品</h2>
        <div class="works-grid">
          <div 
            v-for="work in featuredWorks" 
            :key="work.id" 
            class="work-card"
          >
            <div class="work-image">
              <img 
                v-if="getFeaturedWorkImage(work)"
                :src="getFeaturedWorkImage(work) || undefined"
                :alt="work.title"
                class="work-img"
              />
              <div v-else class="image-placeholder">
                <t-icon name="image" size="40px" />
              </div>
            </div>
            <div class="work-info">
              <h3>{{ work.title }}</h3>
              <p>{{ work.description }}</p>
              <t-button theme="primary" variant="text" @click="viewWork(work.id)">
                查看详情
              </t-button>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- 最新动态 -->
    <section class="latest-updates">
      <div class="page-container">
        <h2 class="section-title">最新动态</h2>
        <div class="updates-timeline">
          <div v-for="update in latestUpdates" :key="update.id" class="timeline-item">
            <div class="timeline-dot"></div>
            <div class="timeline-content">
              <h4>{{ update.title }}</h4>
              <p>{{ update.description }}</p>
              <span class="timeline-date">{{ formatDate(update.date) }}</span>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { useSettingsStore } from '@/stores/settings'
import { useWorksStore } from '@/stores/works'
import { imageConfig } from '@/data/images'

const router = useRouter()
const settingsStore = useSettingsStore()
const worksStore = useWorksStore()

// 使用 settings store 中的精选作品
const featuredWorks = computed(() => settingsStore.featuredWorks)

// 获取精选作品对应的图片
const getFeaturedWorkImage = (work: any) => {
  if (!work) return null
  
  // 首先尝试从 works store 中找到对应的作品
  const targetWork = worksStore.works.find(w => w.id === work.id)
  if (targetWork && targetWork.thumbnail) {
    return targetWork.thumbnail
  }
  
  // 如果没有找到，从 imageConfig 中获取对应分类的默认图片
  if (work.category === 'photography') {
    const photographyImages = imageConfig.works.photography
    if (photographyImages && photographyImages.length > 0) {
      return photographyImages[0].thumbnail // 夕阳下的校园
    }
  }
  if (work.category === 'painting') {
    const paintingImages = imageConfig.works.painting
    if (paintingImages && paintingImages.length > 0) {
      return paintingImages[0].thumbnail // 青春记忆
    }
  }
  if (work.category === 'cityscape') {
    const cityscapeImages = imageConfig.works.cityscape
    if (cityscapeImages && cityscapeImages.length > 0) {
      return cityscapeImages[0].thumbnail // 城市印象1
    }
  }
  if (work.category === 'digital') {
    const digitalImages = imageConfig.works.digital
    if (digitalImages && digitalImages.length > 0) {
      return digitalImages[0].thumbnail // 数字艺术作品1
    }
  }
  return null
}

const latestUpdates = ref([
  {
    id: 1,
    title: '新作品发布',
    description: '完成了新的水彩画作品《春日序曲》',
    date: new Date('2024-12-01')
  },
  {
    id: 2,
    title: '展览参与',
    description: '参加武汉理工大学艺术展览',
    date: new Date('2024-11-15')
  },
  {
    id: 3,
    title: '创作心得',
    description: '分享了关于色彩运用的创作心得',
    date: new Date('2024-11-01')
  }
])

// 查看作品详情 - 使用 string 类型的 ID
const viewWork = (id: string) => {
  // 跳转到作品详情页面
  router.push(`/portfolio/${id}`)
}

const formatDate = (date: Date) => {
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })
}

// 初始化
onMounted(() => {
  settingsStore.initSettings()
  worksStore.initWorks()
})
</script>

<style lang="scss" scoped>
.home {
  min-height: 100vh;
}

.hero {
  margin-bottom: $spacing-md;
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: $spacing-lg;
  align-items: center;
  margin-bottom: 0;
  
  @media (max-width: $breakpoint-md) {
    grid-template-columns: 1fr;
    text-align: center;
  }
  
  .hero-title {
    font-size: 2.2rem;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: $spacing-md;
    
    .title-line {
      display: block;
    }
    
    .highlight {
      color: $accent-color;
    }
    
    @media (max-width: $breakpoint-md) {
      font-size: 1.8rem;
    }
  }
  
  .hero-description {
    font-size: 1rem;
    color: $text-secondary;
    margin-bottom: $spacing-lg;
    line-height: 1.6;
  }
}

.hero-actions {
  display: flex;
  gap: $spacing-md;
  
  @media (max-width: $breakpoint-md) {
    justify-content: center;
    flex-wrap: wrap;
  }
}

.featured-works {
  margin-bottom: $spacing-md;
}

.section-title {
  font-size: 1.6rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: $spacing-xl;
  color: $primary-color;
}

.works-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: $spacing-lg;
}

.work-card {
  background: $glass-bg;
  backdrop-filter: blur(10px);
  border: 1px solid $glass-border;
  border-radius: 12px;
  box-shadow: $glass-shadow;
  border-radius: 12px;
  overflow: hidden;
  transition: all $transition-normal ease;
  
  &:hover {
    transform: translateY(-8px);
    box-shadow: $shadow-lg;
  }
}

.work-image {
  .work-img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .image-placeholder {
    height: 200px;
    background: linear-gradient(135deg, #ffb3d9, #f8d7da);
    display: flex;
    justify-content: center;
    align-items: center;
    color: $text-secondary;
  }
}

.work-info {
  padding: $spacing-md;
  
  h3 {
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: $spacing-xs;
    color: $text-primary;
  }
  
  p {
    color: $text-secondary;
    margin-bottom: $spacing-sm;
    line-height: 1.5;
  }
}

.latest-updates {
  margin-bottom: $spacing-md;
}

.updates-timeline {
  max-width: 800px;
  margin: 0 auto;
}

.timeline-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: $spacing-lg;
  
  .timeline-dot {
    width: 12px;
    height: 12px;
    background: $accent-color;
    border-radius: 50%;
    margin-top: 8px;
    margin-right: $spacing-md;
    flex-shrink: 0;
  }
  
  .timeline-content {
    flex: 1;
    
    h4 {
      font-size: 1.1rem;
      font-weight: 600;
      margin-bottom: $spacing-xs;
      color: $text-primary;
    }
    
    p {
      color: $text-secondary;
      margin-bottom: $spacing-xs;
      line-height: 1.5;
    }
    
    .timeline-date {
      color: $secondary-color;
      font-size: 0.9rem;
    }
  }
}
</style>